import { useEffect, useReducer, useState } from "react";
import { TYPES } from "../actions/platilloAction";
import {
  platilloInitialState,
  platilloReducer,
} from "../reducers/platilloReducer";
import Platillo from "../components/Platillo";
import PlatilloCart from "../components/PlatilloCart";

const Cotizador = () => {
  const [subtotalProd, setSubtotalProd] = useState([]);
  const [total, setTotal] = useState(null)
  const [state, dispatch] = useReducer(platilloReducer, platilloInitialState);
  const { products, cart, totall } = state;



  useEffect(() => {
    state.cart.map((item) => {
      const { subtotal } = item;

      let total1 = [cart.map((item) => item.subtotal)];
      let total7 = [];
      if (cart === []) {
        setSubtotalProd(total7);
        item.subtotal = 0
      } else {
        let total2 = total1[0];

        setSubtotalProd(total2);
      }  

      console.log(item.subtotal)
    });

   
  }, [cart]);

  let total4 = 0;

  const calcularTotal = () => {
    let arraySubtotal = subtotalProd;
    console.log(arraySubtotal);

    if(state.cart === []){
      state.cart.subtotal = 0
    }else{

      for (let i = 0; i < parseInt(arraySubtotal.length); i++) {
        let total5 = parseInt(arraySubtotal[i]);
        total4 += parseInt(total5);
      }
      console.log(parseInt(total4));
    }


  };

  calcularTotal()

  // const calcularTotal = () => {
  //   let arraySubtotal = subtotalProd;
  //   console.log(arraySubtotal);

  //   for (let i = 0; i < parseInt(arraySubtotal.length); i++) {
  //     let total5 = parseInt(arraySubtotal[i]);
  //     total4 += parseInt(total5);
  //   }
  //   console.log(parseInt(total4));

  // };


  

  const addToCart = (id) => {
    // console.log(id, name);
    dispatch({ type: TYPES.ADD_TO_CART, payload: id });

  };


 

  const deleteToPlatillo = (id) => {
    console.log(id);
    // dispatch({type: TYPES.DELETE_TO_PLATILLO, payload: id})
    dispatch({ type: TYPES.DELETE_TO_PLATILLO, payload: id });
  };

  const clearCart = () => {
    dispatch({type: TYPES.CLEAR_CART})
  }

  return (
    <div>
      <h2 className="text-center mt-3 mb-3">Mariscos</h2>
      <div className="container">
        <div className="row">
          <div className="grid-responsive">
            {products.map((product) => (
              <Platillo key={product.id} data={product} addToCart={addToCart} />
            ))}
          </div>

          <div className="">
            <h2>Carrito</h2>
            <button onClick={() => clearCart()} className="btn btn-success">Limpiar carrito</button>
            {cart.map((item, index) => (
              <PlatilloCart
                key={index}
                data={item}
                deleteToPlatillo={deleteToPlatillo}
              />
            ))}
          </div>

          <div className="">
            <h2>Total a pagar</h2>
            <h2>{total4}</h2>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cotizador;
