import coctel from "../assets/camarones/coctel2.png";
import mojoAjo from "../assets/camarones/camarones-mojo-ajo.png";
import caldo from "../assets/camarones/caldo-camarones.png";
import mojarra from "../assets/camarones/mojarra.png";
import { TYPES } from "../actions/platilloAction";
import { Alert } from "bootstrap";

export const platilloInitialState = {
  products: [
    { id: 1, name: "Coctel de camarones", price: 140, avatar: coctel },
    { id: 2, name: "Camarones al mojo de ajo", price: 130, avatar: mojoAjo },
    { id: 3, name: "Caldo de camarones", price: 130, avatar: caldo },
    { id: 4, name: "Mojarra", price: 150, avatar: mojarra },
  ],

  cart: [],

  total1: {total : 0}
};

export function platilloReducer(state, action) {
  switch (action.type) {
    case TYPES.ADD_TO_CART: {
      let newPlatillo = state.products.find(
        (product) => product.id === action.payload
      );
      // console.log(newPlatillo)

      let platilloEnCart = state.cart.find(
        (item) => item.id === newPlatillo.id
      );

      return platilloEnCart
        ? {
            ...state,
            cart: state.cart.map((item) =>
              item.id === newPlatillo.id
                ? {
                    ...item,
                    quantity: item.quantity + 1,
                    subtotal: (item.quantity + 1) * item.price,
                  }
                : item
            ),
          }
        : {
            ...state,
            cart: [
              ...state.cart,
              { ...newPlatillo, quantity: 1, subtotal: newPlatillo.price },
            ],
          };
    }
    case TYPES.REMOVE_ONE_FROM_CART: {
      let itemToDelete = state.cart.find((item) => item.id === action.payload);

      return itemToDelete.quantity > 1
        ? {
            ...state,
            cart: state.cart.map((item) =>
              item.id === action.payload
                ? { ...item, quantity: item.quantity - 1 }
                : item
            ),
          }
        : {
            ...state,
            cart: state.cart.filter((item) => item.id !== action.payload),
          };
    }

    case TYPES.DELETE_TO_PLATILLO: {
      let elementoEliminar = state.cart.find((el) => el.id === action.payload);

      if(state.cart)  {
              return elementoEliminar.quantity > 1
      ? {
          ...state,
          cart: state.cart.map((el) =>
            el.id === action.payload
              ? { ...el, quantity: el.quantity - 1, subtotal: (el.subtotal - el.price) }
              : { el}
          ),
        }
      : {
          ...state,
          cart: state.cart.filter((el) => el.id !== action.payload), 
        };


      }else{
        return elementoEliminar.quantity = 0
        ? {
            ...state,
            cart: state.cart.map((el) =>
              el.id === action.payload
                ? { ...el, quantity: el.quantity - 1, subtotal: 0 }
                : { el}
            ),
          }
        : {
            ...state,
            cart: state.cart.filter((el) => el.id !== action.payload), 
          };

      }
       

      // return elementoEliminar.quantity > 1
      // ? {
      //     ...state,
      //     cart: state.cart.map((el) =>
      //       el.id === action.payload
      //         ? { ...el, quantity: el.quantity - 1, subtotal: (el.subtotal - el.price) }
      //         : { el}
      //     ),
      //   }
      // : {
      //     ...state,
      //     cart: state.cart.filter((el) => el.id !== action.payload), 
      //   };

    //  return elementoEliminar.quantity > 1
    //     ? {
    //         ...state,
    //         cart: state.cart.map((el) =>
    //           el.id === action.payload
    //             ? { ...el, quantity: el.quantity - 1, subtotal: (el.subtotal - el.price) }
    //             : { el}
    //         ),
    //       }
    //     : {
    //         ...state,
    //         cart: state.cart.filter((el) => el.id !== action.payload), 
    //       };
    }

    case TYPES.CLEAR_CART:{
    return   platilloInitialState

    }
    default: return state
  }
}
