import Message from "./Message";

const LetraCancion = ({ lyric, search }) => {
  if (!lyric) return null;
  return (
    <div>
    <br/><br/><br/>
      {lyric.lyrics ? `<p>${lyric.lyrics}</p>` : <Message msg={`Error: no existe la canción "<em>${search.song}</em>"`} bgColor="#dc3545"/>}
    
    </div>
  );
};

export default LetraCancion;
