const Platillo = ({data, addToCart}) => {
    const {id, name, price, avatar} = data
    return(
        <div className="card" style={{ width: "14rem", height: "18rem" }}>
        <figure>
          <img src={avatar} alt="coctel" className="card-img-top" />
          <figcaption className="text-center"><b>{name}</b></figcaption>
          <p className="text-center">{price}</p>
          <button onClick={()=> addToCart(id, name)} className="btn btn-success d-block m-auto">Agregar</button>
        </figure>

        {/* <figure>
        <img src={mojoAjo} alt="coctel" className="w-50" />
          <figcaption>Camarones al mojo de ajo</figcaption>
          <p className="">$130.00</p>
          <button className="btn btn-success ">Agregar</button>
        </figure>

        <figure>
        <img src={caldo} alt="coctel" className="w-50" />
          <figcaption>Caldo de camarones</figcaption>
          <p className="">$110.00</p>
          <button className="btn btn-success ">Agregar</button>
        </figure> */}
      </div>
    )
}

export default Platillo