import Message from "./Message"

const ArtistaBiografia = ({bio, error}) => {
    if(!bio) return null
    return(
        <div>
        {bio.artists ? (bio.artists[0].strBiographyES) : <Message/>}
        { <img src={bio.artists[0].strArtistThumb}></img> }

        </div>
    )
}

export default ArtistaBiografia