const Formulario = () => {
  return (
    <div>
      <h2 className="text-center">Contáctanos</h2>
      <div className="container d-flex justify-content-center">
        <form>
        <label>Nombre</label>
          <input type="text" name="nombre" className="w-100 mb-3"/>
          <label>Apellido paterno</label>
          <input type="text" name="apePat" className="w-100 mb-3"/>
          <label>Apellido materno</label>
          <input type="text" name="apeMat" className="w-100 mb-3"/>
          <label>Correo electrónico</label>
          <input type="email" name="email" className="w-100 mb-3"/>
          <label>Asunto</label>
          <input type="text" name="asunto" className="w-100 mb-3"/>
          <label>Comentarios</label>
          <textarea className="w-100 mb-3"></textarea>
          <button type="submit" className="d-block m-auto w-25">Enviar</button>

        </form>
      </div>
    </div>
  );
};

export default Formulario;
