import { useEffect, useState } from "react";
import ArtistaBiografia from "../components/ArtistaBiografia";
import LetraCancion from "../components/LetraCancion";
import Loader from "../components/Loader";
import Message from "../components/Message";
import SongForm from "../components/SongForm";
import { helpHttp } from "../helpers/helpHttp";

// const initialState = {
//   artista: "",
//   song: "",
// };

const BuscadorCanciones = () => {
  const [search, setSearch] = useState(null);
  const [lyric, setLyric] = useState(null);
  const [bio, setBio] = useState(null);
  const [loading, setLoading] = useState(null);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (search === null) return;
    setLoading(true);

    const fetchData = async () => {
      const { artista, song } = search;
      let artistaUrl = `https://www.theaudiodb.com/api/v1/json/2/search.php?s=${artista}`,
        songUrl = `https://api.lyrics.ovh/v1/${artista}/${song}`;

      let api = helpHttp();

      // let respuesta = await api.get(artistarUrl)

      const [artistaRes, songRes] = await Promise.all([
        api.get(artistaUrl),
        api.get(songUrl),
      ]);
      setBio(artistaRes)
      setLyric(songRes)
      // if (!artistaRes.err) {
      //   setBio(artistaRes);
      //   setError(null);
      // } else {
      //   setError(artistaRes);
      //   console.log(error);
      // }

      // if(!songRes.err){
      //   setLyric(songRes)
      //   setError(null)
      // }else{
      //   setError(songRes)
      //   console.log(error)
      // }
      // setLyric(songRes);
      // console.log(bio.artists)

      setLoading(false);

      console.log(artistaRes);
      console.log(songRes);

      // console.log(artistRes);
      // setBio(artistRes)
      // .then((respuesta) =>
      //    console.log(respuesta.artists[0].strBiographyES)
      //   setBio(respuesta.artists[0].strBiographyES)
      // );
    };

    // const { artista, song } = search;
    fetchData();
  }, [search]);

  const handleSearch = (data) => {
    console.log(data);
    setSearch(data);
  };

  return (
    <div className="container">
      <SongForm handleSearch={handleSearch} />
      {loading && <Loader />}
      {/* {error && <Message />} */}
      <ArtistaBiografia bio={bio} error={error}/>
      <LetraCancion lyric={lyric} error={error} search={search}/>
    </div>
  );
};

export default BuscadorCanciones;
