import { Toast } from "bootstrap";
import { useEffect, useState } from "react";
import { Card } from "react-bootstrap";

const PlatilloCart = ({ data, deleteToPlatillo }) => {
  const { id, name, price, avatar, quantity, subtotal } = data;

  return (
    <div>
      <div className="container" style={{ borderBottom: "1px solid #DDD", marginTop: "10px"}}>
        <div className="row">
          <div className="col-xl-3 col-lg-3 col-md-3 col-sm-6 col-xs-12 p-4">
            {name}
          </div>
          <div className="col-xl-3 col-lg-3 col-md-3 col-sm-6 col-xs-12">
            <img
              src={avatar}
              alt="coctel"
              class="card-img-top"
              style={{ width: "50px" }}
            />
          </div>
          <div className="col-xl-3 col-lg-3 col-md-3 col-sm-6 col-xs-12">
            {`$${price}.00 * ${quantity} = ${subtotal}`}
          </div>
          <div className="col-xl-3 col-lg-3 col-md-3 col-sm-6 col-xs-12">
            <button onClick={()=> deleteToPlatillo(id)} className="btn btn-success">Eliminar</button>
          </div>
        </div>
      </div>
    </div>

    // <div>
    //   <table className="w-100">
    //     <tr className="w-100">
    //       <td className="name">{name}</td>
    //       <td className="avatar"><img src={avatar} alt="coctel" class="card-img-top" style={{width: "50px"}} /></td>
    //       <td className="price">{price}</td>
    //     </tr>
    //   </table>
    // </div>
    // <div class="card" style={{ width: "14rem", height: "18rem" }}>
    //   <figure>
    //     <img src={avatar} alt="coctel" class="card-img-top" />
    //     <figcaption>{name}</figcaption>
    //   </figure>
    //   <div class="card-body">
    //     <h5 class="card-title">${price}.00 * {quantity} = ${subtotal}.00</h5>
    //     <h5 class="card-title">{quantity}</h5>
    //     <button
    //       class="btn btn-success "
    //     >
    //       Eliminar
    //     </button>
    //   </div>
    // </div>
  );
};

export default PlatilloCart;
