const Message = ({msg, bgColor}) => {

  let styles = {
    padding: "1rem",
    marginBottom: "1rem",
    textAlign: "center",
    color: "#fff",
    fontWeigth: "bold",
    backgroundColor: bgColor
  }
  return (
    <div style={styles}>
      {/* <h2>Mensaje de error</h2> */}
      <p dangerouslySetInnerHTML={{ __html: msg }}></p>
    </div>
  );
};

export default Message;
